$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

@import '../../../asset/sass/mixin.scss';
$img: '../../../asset/img/room';
.contribution_list {
    .ant-tabs {
        width: 372px;
        background-color: #ffffff;
    }
    .ant-tabs-bar {
        border-bottom-width: 0;
    }
    .ant-tabs-nav {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        .ant-tabs-tab {
            width: 186px;
            font-size: 14px;
            height: 18px;
            padding: 0;
            margin: 13px auto;
            line-height: normal;
            text-align: center;
            margin-right: 0;
        }
        div {
            .ant-tabs-tab:first-child {
                border-right: 1px solid #ECECEC;
            }
        }
    }
    .ant-tabs-nav .ant-tabs-tab:hover {
        color: #FF0000;
    }
    .ant-tabs-nav .ant-tabs-tab-active {
        color: #FF0000;
    }
    .ant-tabs-ink-bar-animated {
        background: #FF0000;
    }
    .list_pane {
        padding: 0 16px;
        height: 130px;
        &:hover {
            max-height: 424px;
        }
        .item {
            display: -webkit-flex;
            display: flex;
            margin-bottom: 12px;
            -webkit-justify-content: space-between;
                    justify-content: space-between;
            -webkit-align-items: center;
                    align-items: center;
        }
        .left {
            display: -webkit-flex;
            display: flex;
            -webkit-justify-content: flex-start;
                    justify-content: flex-start;
            -webkit-align-items: center;
                    align-items: center;
            .ranking_box {
                width: 19px;
                height: 26px;
                margin-right: 11px;
            }
            .rank_1 {
                @include bg('#{$img}/top_1@2x.png', 19px, 26px, cover);
            }
            .rank_2 {
                @include bg('#{$img}/top_2@2x.png', 19px, 26px, cover);
            }
            .rank_3 {
                @include bg('#{$img}/top_3@2x.png', 19px, 26px, cover);
            }
            .ranking {
                color: #666666;
                font-size: 14px;
                text-align: center;
                line-height: 26px;
            }
            .avatar_box {
                width: 30px;
                height: 30px;
                margin-right: 9px;
                border-radius: 50%;
                overflow: hidden;
                background: #CDCCCA;
            }
            .nickname {
                margin-right: 3px;
                font-size: 14px;
                color: #353535;
                @include ellipse(117px);
            }
        }
        .right {
            .consumption {
                color: #797979;
                font-size: 12px;
            }
        }
    }
}
