$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.third {
  width: 100%;
  height: 100%;
  h4 {
    margin: 6px 0 12px;
    font-family: MicrosoftYaHei;
    font-size: 16px;
    font-weight: 400;
    color: rgba(105, 104, 104, 1);
    text-align: center;
  }
  .btn {
    width: 200px;
    height: 40px;
    margin: 12px auto 0;
    line-height: 40px;
    color: $white;
    text-align: center;
    border-radius: 20px;
  }
  .im {
    background: rgba(52, 153, 209, 1);
  }
  .weChat {
    background: rgba(30, 207, 44, 1);
  }
  h4 {
    margin: 6px 0 12px;
    font-size: 16px;
    font-weight: 400;
    color: rgba(105, 104, 104, 1);
    text-align: center;
  }
  .btn_third {
    width: 200px;
    height: 40px;
    margin: 12px auto 0;
    line-height: 40px;
    color: $white;
    text-align: center;
    border-radius: 20px;
    img {
      display: inline-block;
      width: 24px;
      height: 24px;
    }
  }
  .im {
    background: rgba(52, 153, 209, 1);
  }
  .weChat {
    background: rgba(30, 207, 44, 1);
  }
  .protocol {
    margin-top: 26px;
    font-size: 12px;
    font-weight: 400;
    color: $black;
    text-align: center;
    span {
      color: #31b7ff;
    }
  }
}
