$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

@import "~antd/dist/antd.css";
@import "../../asset/sass/variable.scss";

.mask-modal-login,
.mask-modal-register {
  position: fixed;
  top: 0;
  z-index: 99;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  min-width: 1920px;
  height: 100vh;
  min-height: 916px;
  background: rgba(0, 0, 0, .4);

  -webkit-align-items: center;

          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  .modal {
    width: 700px;
    padding: 22px 50px;
    background: $white;
    border-radius: 6px;
    .tit-close {
      float: right;
      width: 14px;
      height: 14px;
      font-size: 14px;
      line-height: 14px;

      @include color(198, 198, 198, 1);
      @include pointer();
    }
  }
  input.enter {
    width: 300px;
    height: 42px;
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 400;
    text-indent: 16px;
    border: 1px solid rgba(234, 234, 234, 1);
    border-radius: 6px;
  }
  .code {
    input {
      display: inline-block;
      width: 188px;
    }
    .obtain {
      display: inline-block;
      width: 102px;
      height: 42px;
      margin-left: 10px;
      font-size: 14px;
      font-weight: 400;
      line-height: 42px;
      text-align: center;
      border: 1px solid rgba(234, 234, 234, 1);
      border-radius: 6px;

      @include color(130, 130, 130, 1);
      @include pointer();
    }
  }
  .btn {
    width: 300px;
    height: 40px;
    margin: 0 auto;
    margin-top: 37px;
    font-size: 15px;
    font-weight: 400;
    line-height: 40px;
    text-align: center;
    background: $red;
    border-radius: 20px;

    @include color(241, 241, 241, 1);
  }
  .content {
    width: 100%;
    .left {
      display: inline-block;
      width: 350px;
      padding-top: 44px;

      @include color(130, 130, 130, 1);
      .text {
        height: 20px;
        line-height: 20px;
        > div {
          -webkit-align-items: center;
                  align-items: center;
        }
        > div:first-child {
          display: inline-block;
          width: 220px;
        }
        > div:last-child {
          display: inline-block;
          width: 80px;
          text-align: right;
        }
      }
      > div {
        width: 300px;
        height: 42px;
        margin-bottom: 10px;
      }
      .sex {
        height: 28px;
        margin-top: 5;
        span,
        p {
          display: inline-block;
          height: 28px;
          line-height: 28px;

          @include pointer();
        }
        span {
          margin-right: 31px;
          font-size: 14px;

          @include color(111, 111, 111, 1);
        }
        p {
          width: 58px;
          height: 28px;
          margin-bottom: 0;
          text-align: center;
          border: 1px solid rgba(234, 234, 234, 1);
          border-radius: 14px;

          @include color(130, 130, 130, 1);
          img {
            display: inline-block;
            width: 16px;
            height: 16px;
          }
        }
        p:last-child {
          margin-left: 22px;
        }
        p.active {
          color: $white;
          background: rgba(255, 89, 140, 1);
          border: none;
        }
      }
      .verif {
        height: 28px;
        margin-bottom: 0;
        p {
          display: -webkit-flex;
          display: flex;
          margin-bottom: 0;

          -webkit-align-items: center;

                  align-items: center;
          @include color(130, 130, 130, 1);
          @include pointer();
          span {
            display: -webkit-inline-flex;
            display: inline-flex;
            width: 12px;
            height: 12px;
            margin-right: 3px;
            background: #b3eeff;
            border-radius: 50%;

            -webkit-align-items: center;

                    align-items: center;
            -webkit-justify-content: center;
                    justify-content: center;
            i {
              display: inline-block;
              width: 6px;
              height: 6px;
              background: rgba(58, 154, 234, 1);
              border-radius: 50%;
            }
          }
        }
      }
      .live {
        display: -webkit-flex;
        display: flex;
        height: 28px;
        color: #828282;

        -webkit-align-items: center;

                align-items: center;
        a {
          color: #444;
          text-decoration: underline;
        }
      }
    }
  }
  .bind-phone {
    width:400px;
    height:383px;
    padding: 20px;
    background:$white;
    border-radius:6px;
    .btn-close{
      display: -webkit-flex;
      display: flex;
      height: 12px;

      -webkit-align-items: center;

              align-items: center;
      -webkit-justify-content: flex-end;
              justify-content: flex-end;
      span {
        display: inline-block;
        width: 12px;
        height: 12px;
        background: url('../../asset/img/login/btm_close_nor@2x.png');
      }
    }
    .cont {
      width: 300px;
      margin: 0 auto;
      h5{
        height:44px;
        margin-bottom: 0;
        font-size:24px;
        font-weight:400;
        line-height: 44px;
        color:rgba(67,67,67,1);
        text-align: center;
        border-bottom: 1px solid rgba(241,241,241,1);
      }
      .inp-box {
        margin: 60px auto;
      }
    }
  }
}
.mask-modal-login {
  .modal {
    height: 383px;
  }
}
.mask-modal-register {
  .modal {
    height: 575px;
  }
}

.modal {
  .ant-tabs-nav {
    .ant-tabs-tab {
      height: 48px;
      font-family: MicrosoftYaHei;
      font-size: 24px;
      font-weight: 400;
      line-height: 48px;
      color: rgba(67, 67, 67, 1);
      &:hover {
        color: $red;
      }
    }
  }
  .ant-tabs.ant-tabs-card .ant-tabs-card-bar {
    .ant-tabs-tab {
      background: $white;
      border: none;
    }
    .ant-tabs-tab-active {
      padding-bottom: 0;
      color: $red;
      border-bottom: 2px solid $red;
    }
    .ant-tabs-nav-container {
      height: 49px;
    }
  }
}
