$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}


.header_wrap {
  width: 100%;
  height: 60px;
  font-size: 12px;
  color: #5e5f60;
  text-align: center;
  background-color: #fff;
  .container {
    display: -webkit-flex;
    display: flex;
    width: 1400px;
    height: 60px;
    margin: 0 auto;

    -webkit-align-items: center;

            align-items: center;
    .logo {
      width: 143px;
      height: 36px;
    }

    .navbar {
      ul {
        width: 100%;
        height: 100%;
        padding-left: 0;
        margin-left: 55px;

        li {
          float: left;
          height: 32px;
          margin-left: 15px;
          font-size: 18px;
          font-weight:400;
          line-height: 32px;
          list-style: none;
          cursor: pointer;
          a {
            padding: 7px 12px;
            color: #000;
            text-decoration:none;
          }
        }
        .active {
          color: #fff;
          background: $red;
          border-radius: 18px;
          a {
            color: #fff;
          }
        }
      }
    }

    .search_input {
      margin-left: 40px;
      .ant-input {
        border-radius: 32px;

        -webkit-border-radius: 32px;
        -moz-border-radius: 32px;
        -ms-border-radius: 32px;
        -o-border-radius: 32px;
      }
      .ant-input::-webkit-input-placeholder {
        color: #727171;
      }
    }

    .action_btn {
      margin-left: 380px;
      img {
        display: inline-block;
        width: 76px;
        height: 36px;
        margin: 0 11px;
      }
    }


    .user:hover .anticon-down > svg{
      -webkit-transform: rotate(180deg);
      transform: rotate(180deg);
    }

    .user {
      display: -webkit-flex;
      display: flex;
      height: 100%;

      -webkit-justify-content: center;

              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      .user_info {
        position: relative;
        .user_name {
          padding: 0 6px;
        }
        .user_card {
          position: absolute;
          top: 45px;
          left: -130%;
          z-index: 99;
          display: none;
          width: 324px;
          height: 434px;
          background-color: #fff;
          border-radius:6px;

          -webkit-border-radius:6px;
          -moz-border-radius:6px;
          -ms-border-radius:6px;
          -o-border-radius:6px;
          .user_info {
            display: -webkit-flex;
            display: flex;
            width: 324px;
            height: 277px;
            padding-top: 30px;
            color: #fff;
            -webkit-flex-direction: column;
                    flex-direction: column;
            background: url('../../asset/img/header/my_bg@2x.png') no-repeat;
            background-size: cover;

            -webkit-align-items: center;

                    align-items: center;
            p {
              padding-top: 16px;
              .anticon-form {
                color: #fff;
              }
            }
            .progress {
              display: -webkit-flex;
              display: flex;
              margin-top: 24px;

              -webkit-justify-content: center;

                      justify-content: center;
              -webkit-align-items: center;
                      align-items: center;
              .progress {
                position: relative;
                margin: 0 10px;
                .progress_percent {
                  position: absolute;
                  font-size: 10px;
                  color: #868687;
                }
              }
            }
          }
          .quit {
            position: absolute;
            top: 18px;
            right: 18px;
            color: #fff;
            cursor: pointer;
          }
          .user_wallet {
            width: 100%;
            height: 59px;
            padding: 0 16px;
            font-size: 14px;
            line-height: 59px;
            color: #5e5f60;
            text-align: left;
            box-shadow:0 2px 1px 0 rgba(1,14,31,.06);
            .wallet_money {
              display: inline-block;
              width: 180px;
              padding-left: 32px;
              font-size: 16px;
              color: $red;
              background: url('../../asset/img/header/icon_haozuan@2x.png') no-repeat;
              background-position:8px center;
              background-size: auto;
            }
            .recharge_btn {
              display: inline-block;
              width:56px;
              height:24px;
              line-height: 24px;
              color: #fff;
              text-align: center;
              background:rgba(239,7,22,1);
              border-radius:12px;
            }
          }
          .user_btn {
            display: -webkit-flex;
            display: flex;
            height: 97px;
            color: #5e5f60;

            -webkit-justify-content: center;

                    justify-content: center;
            -webkit-align-items: center;
                    align-items: center;
            .user_center {
              cursor: pointer;
            }
          }
        }
        .user_card::after {
          position: absolute;
          top: -13px;
          left: 50%;
          z-index:1;
          display: block;
          width:0;
          height: 0;
          content: "";
          border: 7px solid transparent;
          border-bottom-color: #000;
        }
        .grade_img {
          .sex {
            display: inline-block;
            width: 20px;
            height: 20px;
          }
          .grade {
            display: inline-block;
            width: 40px;
            height: 20px;
            margin-left: 8px;
          }
        }
      }
    }
    .user:hover .user_card {
      display: block;
    }
    .tourist {
      display: -webkit-flex;
      display: flex;

      -webkit-justify-content: center;

              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      span {
        padding: 0 10px;
        cursor: pointer;
      }
      span:first-of-type {
        border-right: 2px solid #e8e8e8;
      }
    }
  }
}
