$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

$img: '../../asset/img/index/';
.anchor_list_container {
  width: 1026px;
  .header {
    display: -webkit-flex;
    display: flex;
    margin-bottom: 13px;

    -webkit-justify-content: flex-start;

            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    .badge {
      width: 30px;
      height: 30px;
      margin-right: 13px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      &.follow {
        background-image: url('#{$img}/icon_follow@2x.png');
      }
      &.recommend {
        background-image: url('#{$img}/icon_recommend@2x.png');
      }
    }
    p {
      font-size: 18px;
      color: black;
    }
  }
  .list_container {
    display: -webkit-flex;
    display: flex;

    -webkit-flex-wrap: wrap;

            flex-wrap: wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    .anchor {
      margin-right: 14px;
      margin-bottom: 14px;
    }
    .anchor:nth-child(5n+0) {
      margin-right: 0;
    }
  }
  >.tips {
    margin-top: 47px;
    font-size: 14px;
    color: #5e5f60;
    text-align: center;
  }
}
