$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.login-container {
  width: 100%;
  height: auto;
  > .left-container {
    display: inline-block;
    width: 350px;

    @include color(130, 130, 130, 1);
    }
  > .right-container {
    display: block;
    float: right;
    width: 250px;
    height: 238px;
    border-left: 1px solid rgba(238, 238, 238, 1);
    }
  }
