div,
main,
input {
    box-sizing: border-box;
}

/* 清除移动端点击时元素闪烁 */
div, button, ul, li, p, input, img {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

i {
    font-style: normal;
}

body p,
body ul {
    margin: 0;
}

img {
    display: block;
}

.float_left {
    float: left;
}

.float_right {
    float: right;
}

.clear {
    clear: both;
}

.hide {
    display: none;
}

a {
    text-decoration: none;
}

body {
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    font-family: "PingFang SC Bold", "Helvetica Neue", Helvetica, STHeitiSC-Light, "Microsoft YaHei", Arial, sans-serif;
    font-weight: 400;
}

input[type=number],
input[type=password],
input[type=text],
textarea {
    -webkit-appearance: none;
}

input:focus {
    outline: none;
}

button {
    border: none;
}

button:focus {
    border: none;
    outline: none;
}

.show {
  display: block;
}

.hide {
  display: none;
}
