$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

@import '../../../asset/sass/mixin.scss';
$img: '../../../asset/img/download';
$asset: '../../../asset/img';
.download_container {
  width: 100%;
  height: 722px;
  background: url('#{$img}/bg@2x.png') center center no-repeat;
  background-size: cover;
  .container {
    display: -webkit-flex;
    display: flex;
    width: 1000px;
    padding-top: 44px;
    margin: 0 auto;

    -webkit-justify-content: space-between;

            justify-content: space-between;
    .left {
      @include bg('#{$img}/phone@2x.png',
      447px,
      639px,
      cover);
    }
    .right {
      .slogan {
        margin-top: 124px - 44px;
        margin-bottom: 83px;

        @include bg('#{$img}/txt@2x.png',
        664px,
        62px,
        cover);
      }
      .qrcode {
        display: -webkit-flex;
        display: flex;
        margin-bottom: 70px;

        -webkit-justify-content: center;

                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        .qrcode_img {
          margin-right: 21px;

          @include bg('#{$asset}/header/qr_code.png',
          138px,
          138px,
          cover);
        }
        .desc {
          font-size: 18px;
          line-height: 1.2;
          color: #fff;
          .big {
            margin-bottom: 10px;
            font-size: 22px;
            font-weight: bolder;
          }
        }
      }
      .qq {
        display: -webkit-flex;
        display: flex;
        width: 328px;
        height: 74px;
        padding: 0 6px;
        margin: 0 auto;
        font-size: 16px;
        color: white;
        background: rgba(241, 45, 103, 1);
        border-radius: 37px;

        -webkit-justify-content: flex-start;

                justify-content: flex-start;
        -webkit-align-items: center;
                align-items: center;
        .qq_img {
          margin-right: 21px;

          @include bg('#{$img}/qq@2x.png',
          60px,
          60px,
          cover);
        }
      }
    }
  }
}
