$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

@import '../../../asset/sass/mixin.scss';
@import '../../../asset/sass/variable.scss';
$com: '../../../asset/img/com';
// set variable
.chat_plate_box {
    .ant-tabs {
        width: 372px;
        background-color: #ffffff;
    }
    .ant-tabs-bar {
        border-bottom-width: 0;
    }
    .ant-tabs-nav {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        .ant-tabs-tab {
            width: 186px;
            font-size: 14px;
            height: 18px;
            padding: 0;
            margin: 13px auto;
            line-height: normal;
            text-align: center;
            margin-right: 0;
        }
        div {
            .ant-tabs-tab:first-child {
                border-right: 1px solid #ECECEC;
            }
        }
    }
    .ant-tabs-nav .ant-tabs-tab:hover {
        color: #FF0000;
    }
    .ant-tabs-nav .ant-tabs-tab-active {
        color: #FF0000;
    }
    .ant-tabs-ink-bar-animated {
        background: #FF0000;
    }
    .pane_box {
        position: relative;
        padding: 0 16px;
        height: 539px;
        overflow: hidden;
        .management_box {
            .item {
                display: -webkit-flex;
                display: flex;
                margin-bottom: 12px;
                -webkit-justify-content: space-between;
                        justify-content: space-between;
                -webkit-align-items: center;
                        align-items: center;
                .left {
                    display: -webkit-flex;
                    display: flex;
                    -webkit-justify-content: flex-start;
                            justify-content: flex-start;
                    -webkit-align-items: center;
                            align-items: center;
                    .badge {
                        @include bg('#{$com}/badge_manager@2x.png', 33px, 18px, cover);
                        margin-right: 6px;
                    }
                    .avatar_box {
                        width: 30px;
                        height: 30px;
                        margin-right: 9px;
                        border-radius: 50%;
                        overflow: hidden;
                        background: #CDCCCA;
                    }
                    .nickname {
                        margin-right: 3px;
                        font-size: 14px;
                        color: #353535;
                        @include ellipse(117px);
                    }
                }
            }
        }
    }
    .chat_input_box {
        position: absolute;
        bottom: 0;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        width: 340px;
        height: 50px;
        margin: 10px auto;
        .ant-input:focus {
            border: 1px solid rgba(232, 232, 232, 1);
            box-shadow: 0 0 0;
        }
        .chat_input {
            box-sizing: border-box;
            height: 50px;
            border-radius: 6px 0px 0px 6px;
            background: rgba(250, 250, 250, 1);
            border: 1px solid rgba(232, 232, 232, 1);
            border-right-width: 0;
        }
        .btn_send {
            width: 68px;
            height: 100%;
            background: $red;
            text-align: center;
            line-height: 50px;
            border-radius: 0px 6px 6px 0px;
            font-size: 16px;
            color: #ffffff;
        }
        .sign_tips {
            position: absolute;
            width: 340px - 60px;
            height: 50px;
            left: 0px;
            top: 25px;
            border-radius: 6px 0px 0px 6px;
            background: rgba(250, 250, 250, 1);
            border: 1px solid rgba(232, 232, 232, 1);
            border-right-width: 0;
            color: #A2A2A2;
            font-size: 16px;
            padding-left: 20px;
            line-height: 50px;
            -webkit-transform: translateY(-50%);
                    transform: translateY(-50%);
            .red {
                color: $red;
                cursor: pointer;
            }
        }
    }
}
