// 背景图
@mixin bg($url, $w, $h, $model) {
    width: $w;
    height: $h;
    background: url($url) center no-repeat;
    background-size: $model;
}

// 单行省略号
@mixin ellipse($w) {
    max-width: $w;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
