$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.anchor_show_card_container {
    .ant-card {
        position: relative;
    }
    .ant-card-cover {
        position: relative;
        width: 100%;
        overflow: hidden;
        border-radius: 2px 2px 0 0;
    }
    .anchor_cover {
        position: relative;
        top: 50%;
        width: 100%;
        height: auto;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
    }
    .ant-card-body {
        padding: 6px 8px;
        .tips {
            position: absolute;
            top: 6px;
            right: 6px;
            width: 60px;
            height: 26px;
            font-size: 12px;
            font-weight: bold;
            line-height: 26px;
            color: rgba(243, 241, 239, 1);
            text-align: center;
            background: rgba(0, 0, 0, 1);
            border-radius: 13px;
            opacity: .4;
        }
        .cover {
            position: absolute;
            top: 0;
            left: 0;
            width: 194px;
            height: 194px;
            border-radius: 2px 2px 0 0;
            background: rgba($color: #000000, $alpha: 0);
            -webkit-transition: .2s;
            transition: .2s;
            .btn_play {
                position: absolute;
                top: 68px;
                left: 50%;
                width: 58px;
                height: 58px;
                background: url('../../asset/img/index/icon_enter@2x.png') center center no-repeat;
                background-size: cover;
                opacity: 0;
                -webkit-transition: 0.3s;
                transition: 0.3s;
                -webkit-transform: translateX(-50%) scale(1.5);
                        transform: translateX(-50%) scale(1.5);
                -webkit-transform-origin: center;
                        transform-origin: center;
            }
            &:hover {
                background: rgba($color: #000000, $alpha: .3);
            }
            &:hover .btn_play {
                -webkit-transform: translateX(-50%) scale(1);
                        transform: translateX(-50%) scale(1);
                opacity: 1;
            }
        }
    }
    .content {
        font-size: 11px;
        color: #5e5f60;
        .head {
            display: -webkit-flex;
            display: flex;
            margin-bottom: 4px;
            -webkit-justify-content: space-between;
                    justify-content: space-between;
            -webkit-align-items: center;
                    align-items: center;
        }
        .desc {
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
        }
    }
}
