$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.footer_container {
  width: 100%;
  padding: 25px 0 10px;
  font-size: 11px;
  color: #8f8f8f;
  text-align: center;
  background: #313131;
  p {
    margin-bottom: 12px;
    }
  }
