$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.room_box {
    width: 1400px;
    margin: 0 auto;
    .header {
        margin-top: 20px;
        margin-bottom: 40px;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center;
        .left {
            width: 1016px;
            height: 797px;
            background: #ffffff;
        }
        .right {
            .contribution_list--box {
              margin-bottom: 10px;
            }
        }
    }
}
